<template>
	<view class="page">
		
		<view class="input-t">
			<text>内容</text>
			<text class="input-quick"></text>
		</view>
		<view class="input-body">
			<textarea v-model="form.content" placeholder="请输入内容..." class="input-textare" />
		</view>
		
		
		<button
			class="confirm-btn"
			:class="'bg-' + themeColor.name"
			:disabled="btnLoading"
			:loading="btnLoading"
			@tap="send"
		>
			提交
		</button>

	</view>
</template>

<script>
	import moment from '@/common/moment';
	import requests from '@/utils/requests.js'
	export default {
		data() {
			return {
				repairDetail:{},
				sendDate: {
					remind: '0',
					content: ''
				},
				type: '',
				form: {
					content: '',
					articleId: '',
					pid: '',
					userId: uni.getStorageSync('userInfo').userId
				},
			};
		},
		onLoad(e) {
			console.log(e)
			if(e.type){
				this.type = Number(e.type)
				if(this.type === 1){
					uni.setNavigationBarTitle({
					    title: '评论'
					});
				}else{
					uni.setNavigationBarTitle({
					    title: '回复'
					});
				}
			}
			if(e.repairDetail){
				this.repairDetail = JSON.parse(e.repairDetail)
				console.log(this.repairDetail)
				this.form.pid = this.repairDetail.id
				this.form.articleId = this.repairDetail.articleId
			}
			
			if(e.articleId){
				this.form.articleId = e.articleId
			}
			
		},
		onShow() {
			uni.setNavigationBarColor({
				frontColor: '#ffffff',
				backgroundColor: this.themeColor.color,
				animation: {
					duration: 400,
					timingFunc: 'easeIn'
				}
			})
		},

		methods: {
			send(){
				if (this.form.content === '' || this.form.content === null) {
					uni.showToast({
						title: '请输入内容',
						icon: 'none'
					})
					return
				}
				let obj = '';
				if(this.type === 1){ // 新增评论
					obj = Object.assign({
						content: this.form.content,
						articleId: this.form.articleId,
						userId: this.form.userId
					})
					console.log(obj)
				}else if(this.type === 2){ // 回复评论
					obj = Object.assign({
						content: this.form.content,
						articleId: this.form.articleId,
						pid: this.form.pid,
						userId: this.form.userId
					})
					console.log(obj)
				}
				requests.addCommenttion(obj).then(res => {
					if(res && res.code === 200){
						uni.showToast({
							title: res.msg,
							icon: 'success',
						})
					}
				})
			},
		}
	};
</script>

<style lang="scss">
	page {
		background-color: $page-color-base;
	}

	.date {
		height: 80upx;
		line-height: 80upx;
		font-size: $font-base + 2upx;
		color: $font-color-dark;
	}

	/*表单标题*/
	.input-t {
		display: flex;
		flex-direction: row;
		justify-content: space-between;
		align-items: center;
		padding: $spacing-base;
		margin-top: $spacing-base;
		font-size: $font-base;

	}

	.input-quick {
		position: relative;
		padding-right: 40upx;

		.iconfont {
			font-size: $font-sm;
		}
	}

	.input-body {
		background: $color-white;
		padding: $spacing-sm $spacing-base;

		.gender-item {
			margin-right: 20upx;

			.gender-item-text {
				padding-left: 10upx;
			}

		}
	}

	.input-textare {
		height: 200upx;
		font-size: 34upx;
		line-height: 50upx;
		width: 100%;
		box-sizing: border-box;
		padding: 20upx 30upx 0;
	}
</style>
